@import '../../../../theme.scss';

.root {
  padding-top: 33vh;
  @media (max-width: $breakpoint) {
    height: 100vh;
  }
  position: relative;
  height: 100vh;
  width: 100%;
}

.titles {
  text-align: center;
  > h1 {
    width: 50rem;
    font-size: 3.6rem;
    margin-bottom: 1.2rem;
    color: white;
    letter-spacing: 0.144rem;
  }
  > h2 {
    font-size: 1.4rem;
    color: #9e9e9e;
  }
  @media (max-width: $breakpoint) {
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    > h1 {
      width: 15.2rem;
      font-size: 1.6rem;
      line-height: 1.875rem;
      margin-bottom: 0.4rem;
      color: white;
      word-break: break-word;
      letter-spacing: normal;
      > br {
        display: none;
      }
    }
    > h2 {
      font-size: 0.9rem;
      line-height: 1.05rem;
      color: #9e9e9e;
      word-break: break-word;
    }
  }
}

.scrollMore {
  position: absolute;
  bottom: 2.7vh;
  font-size: 0.7rem;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  > div {
    opacity: 0.72;
    margin-bottom: 0.8rem;
  }
  > img {
    height: 2rem;
    width: 2rem;
  }
  @media (max-width: $breakpoint) {
    bottom: 0;
    > img {
      height: 2rem;
      width: 2rem;
    }
  }
}
