@import '../../../../theme.scss';

.team {
  position: relative;
  width: 100%;
  padding-top: 14.6vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  @media (max-width: $breakpoint) {
    padding-top: 8rem;
  }
  > div:nth-child(1) {
    width: 8.4rem;
    height: 1.6rem;
    letter-spacing: 0.1rem;
    border-radius: 0.2rem;
    background-color: rgba(202, 199, 244, 0.19);
    display: flex;
    align-items: center;
    justify-content: center;
    color: rgba(172, 176, 217, 0.8);
    font-size: 0.7rem;
    font-weight: bold;
    margin-bottom: 1.2rem;
    @media (max-width: $breakpoint) {
      font-size: 0.6rem;
      width: 7.6rem;
      height: 1.4rem;
      margin-bottom: 1.2rem;
    }
  }
  > div:nth-child(2) {
    font-size: 2rem;
    font-weight: bold;
    color: #ffffff;
    margin-bottom: 1.2rem;
    letter-spacing: 0.04rem;
    @media (max-width: $breakpoint) {
      text-align: center;
      margin-bottom: 1.2rem;
      font-size: 1.1rem;
    }
  }
  > div:nth-child(3) {
    font-size: 1rem;
    opacity: 0.9;
    color: #ffffff;
    margin-bottom: 3.6rem;
    line-height: 1.6;
    letter-spacing: 0.02rem;
    @media (max-width: $breakpoint) {
      text-align: center;
      margin-bottom: 1.4rem;
      font-size: 0.8rem;
      word-wrap: break-word;
      width: 16.4rem;
      br {
        display: none;
      }
    }
  }
  > div:nth-child(4) {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
}

.profile {
  position: relative;
  text-align: center;
  width: 15.6rem;
  height: 20.4rem;
  margin-bottom: 3.6rem;
  > div:nth-child(2) {
    width: 100%;
    font-size: 1.2rem;
    font-weight: 600;
    height: 2rem;
    color: #ffffff;
    opacity: 0.95;
    @media (max-width: $breakpoint) {
      line-height: 1.2rem;
      font-size: 1rem;
      height: 1.2rem;
      margin-bottom: 0.2rem;
    }
  }
  > div:nth-child(3) {
    width: 100%;
    font-size: 0.8rem;
    color: #a6a6a6;
    white-space: pre-wrap;
    line-height: 1.5;
    letter-spacing: 0.024rem;
    @media (max-width: $breakpoint) {
      line-height: 0.8rem;
      font-size: 0.6rem;
    }
  }
  @media (max-width: $breakpoint) {
    width: 9.3rem;
    height: 12.55rem;
  }
}
.profileImage {
  > img {
    width: 15.6rem;
    height: 15.6rem;
    background-color: #d1d2d5;
    @media (max-width: $breakpoint) {
      width: 9.3rem;
      height: 9.3rem;
    }
  }
  > div {
    @media (max-width: $breakpoint) {
      width: 9.3rem;
      height: 9.3rem;
    }
    transition: all 0.1s;
    position: absolute;
    width: 15.6rem;
    height: 15.6rem;
    top: 0;
    left: 0;
    opacity: 0;
    font-size: 1.5rem;
    background-color: rgba(0, 0, 0, 0.6);
    a {
      color: rgba(255, 255, 255, 0.7);
    }
    a + a {
      margin-left: 0.8rem;
    }
    display: flex;
    align-items: center;
    justify-content: center;
  }
  margin-bottom: 0.8rem;
}
.profileImage:hover > div {
  opacity: 1 !important;
}
