@import '../../theme.scss';

.navBar {
  z-index: 11;
  position: sticky;
  top: 0;
  width: 100%;
  height: 8vh;
  margin-bottom: -8vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  img {
    height: 0.8rem;
    width: auto;
  }
  > div {
    display: flex;
    align-items: flex-end;
  }
  .backedby {
    font-size: 0.8rem;
    color: #ffffff;
    opacity: 0.72;
  }
  @media (max-width: $breakpoint) {
    justify-content: center;
    height: 2.4rem;
    margin-bottom: -2.4rem;
    img {
      height: 0.8rem;
    }
    > div {
      display: none;
    }
  }
}
