@import '../../theme.scss';

html {
  font-size: unquote('min(1.389vw, 2.22vh)');
  font-family: Roboto;
}

@media (max-width: $breakpoint) {
  html {
    font-size: 5.333vw;
  }
}

.mainpage {
  width: 100vw;
  position: absolute;
  top: 0;
  overflow-x: hidden;
  overflow-y: auto;
  height: 100vh;
}

@media (max-width: $breakpoint) {
  .container {
    padding-left: calc(50vw - 1.75rem);
    padding-right: calc(50vw - 1.75rem);
  }
}

.container {
  padding-left: calc(50vw - 25rem);
  padding-right: calc(50vw - 25rem);
  width: 100vw;
  position: absolute;
  top: 0;
}
.logoBackground {
  position: sticky;
  top: 0;
  width: 100%;
  height: 8vh;
  margin-bottom: -8vh;
  background-color: black;
  z-index: 10;
  @media (max-width: $breakpoint) {
    height: 2.4rem;
    margin-bottom: -2.4rem;
  }
}

.wallpaper {
  position: absolute;
  opacity: 0.5;
  top: 0;
  left: 0;
  right: 0;
  height: 100vh;
  z-index: -1;
}

.contectUs {
  margin-top: 2rem;
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 2rem;
  @media (max-width: $breakpoint) {
    margin-top: 6.4rem;
    flex-direction: column;
    justify-content: normal;
  }
  > div {
    font-size: 1.2rem;
    font-weight: bold;
    color: #ffffff;
    @media (max-width: $breakpoint) {
      font-size: 1.1rem;
      margin-bottom: 2.4rem;
    }
  }
  > a:nth-child(2) {
    display: flex;
    text-decoration: none;
    align-items: center;
    font-size: 1rem;
    color: #ffffff;
    line-height: 1rem;
    svg {
      height: 2rem;
      width: 2rem;
      color: #ffffff;
      margin-left: 0.8rem;
    }
    @media (max-width: $breakpoint) {
      font-size: 0.8rem;
      line-height: 0.8rem;
      svg {
        height: 1.2rem;
        width: 1.2rem;
        margin-left: 0.4rem;
      }
    }
  }
}

footer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  color: #ffffff;
  opacity: 0.9;
  font-size: 0.7rem;
  letter-spacing: 0.024rem;
  > div + div {
    margin-left: 1.2rem;
  }
  margin-top: 1.6rem;
  margin-bottom: 1.6rem;
  @media (max-width: $breakpoint) {
    margin-top: 0;
    margin-bottom: 0;
    flex-direction: column;
    height: 4.2rem;
    justify-content: center;
    font-size: 0.6rem;
    > div + div {
      margin-left: 0;
      margin-top: 0.4rem;
    }
  }
}
.footerHr {
  border-top: 0.1rem solid #3f00ff;
  border-bottom: none;
  position: relative;
  left: -50vw;
  width: 200vw;
}
