@import '../../../../theme.scss';

.investment {
  position: relative;
  height: 30.56vw;
  display: flex;
  flex-direction: column;
  justify-content: center;

  > img {
    position: absolute;
    top: 0;
    left: calc(25rem - 50vw);
    width: 100vw;
    height: auto;
    z-index: -1;
  }
  @media (max-width: $breakpoint) {
    display: block;
    height: auto;
    padding-top: 3.2rem;
    > img {
      left: 0;
      width: auto;
      height: 24rem;
    }
  }
}
.investment > div {
  display: flex;
  justify-content: space-between;
  @media (max-width: $breakpoint) {
    display: block;
  }
}

.leftCol {
  @media (max-width: $breakpoint) {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  > div:nth-child(1) {
    width: 6.8rem;
    height: 1.6rem;
    letter-spacing: 0.1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0.2rem;
    background-color: rgba(255, 255, 255, 0.16);
    font-size: 0.7rem;
    font-weight: bold;
    color: rgba(255, 255, 255, 0.8);
    margin-bottom: 1.2rem;
    @media (max-width: $breakpoint) {
      width: 6.8rem;
      height: 1.4rem;
      font-size: 0.6rem;
    }
  }
  > div:nth-child(2) {
    line-height: 1.4;
    letter-spacing: 0.04rem;
    font-size: 2rem;
    font-weight: bold;
    color: #ffffff;
    line-height: 1.4;
    > span {
      color: #5bf1a0;
    }
    margin-bottom: 3.2rem;
    @media (max-width: $breakpoint) {
      text-align: center;
      font-size: 1.1rem;
      margin-bottom: 2.4rem;
    }
  }
  > a:nth-child(3) {
    display: flex;
    text-decoration: none;
    align-items: center;
    color: #5bf1a0;
    font-size: 1rem;
    line-height: 1rem;
    font-weight: bold;
    letter-spacing: 0.02rem;
    svg {
      color: #5bf1a0;
      width: 2rem;
      height: 2rem;
      margin-left: 0.8rem;
    }
    @media (max-width: $breakpoint) {
      svg {
        font-size: 0.8rem;
        line-height: 0.8rem;
        width: 1.2rem;
        height: 1.2rem;
        margin-left: 0.4rem;
      }
    }
  }
}

.rightCol {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3rem;
  > div:nth-child(1) {
    font-size: 0.7rem;
    font-weight: 900;
    color: #ebebeb;
    letter-spacing: 0.014rem;
    margin-bottom: 0.8rem;
    @media (max-width: $breakpoint) {
      font-size: 0.5rem;
    }
  }
  @media (max-width: $breakpoint) {
    margin-top: 2.5rem;
  }
}
