@import '../../../../theme.scss';

.root {
  position: relative;
  height: 100vh;
  width: 100%;
  padding-top: 5rem;
  @media (max-width: $breakpoint) {
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.demoIntro {
  padding-top: 31.3vh;
  @media (max-width: $breakpoint) {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding-top: 0;
  }
  > div:nth-child(1) {
    @media (max-width: $breakpoint) {
      width: 5.6rem;
      height: 1.4rem;
      font-size: 0.6rem;
      margin-bottom: 0.8rem;
    }
    font-weight: bold;
    letter-spacing: 0.1rem;
    width: 6.4rem;
    height: 1.6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0.2rem;
    background-color: rgba(202, 199, 244, 0.19);
    font-size: 0.7rem;
    color: #acb0d9;
    opacity: 0.8;
    margin-bottom: 1.2rem;
  }
  > div:nth-child(2) {
    @media (max-width: $breakpoint) {
      font-size: 1.1rem;
      line-height: 1.5rem;
      margin-bottom: 0.8rem;
    }
    letter-spacing: 0.04rem;
    color: #ffffff;
    font-weight: bold;
    font-size: 2rem;
    > span {
      color: #faac14;
    }
    margin-bottom: 1.2rem;
  }
  > div:nth-child(3) {
    @media (max-width: $breakpoint) {
      font-size: 0.8rem;
      line-height: 1.2rem;
      margin-bottom: 2.4rem;
      line-height: 1.2;
    }
    letter-spacing: 0.04rem;
    line-height: 1.6;
    color: #ffffff;
    font-size: 1rem;
    margin-bottom: 3.2rem;
  }
  > a:nth-child(4) {
    text-decoration: none;
    color: #faac14;
    font-weight: bold;
    font-size: 1rem;
    line-height: 1rem;
    display: flex;
    align-items: center;
    svg {
      color: #faac14;
      height: 2rem;
      width: 2rem;
      margin-left: 0.8rem;
    }
    @media (max-width: $breakpoint) {
      font-size: 0.8rem;
      line-height: 0.8rem;
      > a {
        margin-left: 0.4rem;
      }
      svg {
        height: 1.2rem;
        width: 1.2rem;
      }
    }
  }
}

.images {
  > img:nth-child(1) {
    height: 61.1vh;
    width: auto;
    position: absolute;
    top: 20vh;
    left: 29rem;
    border-radius: 0.4rem;
  }
  > img:nth-child(2) {
    height: 18.6vh;
    width: auto;
    position: absolute;
    top: 65.3vh;
    left: 24rem;
  }
  @media (max-width: $breakpoint) {
    margin-top: 2.4rem;
    position: relative;
    > img:nth-child(1) {
      position: static;
      width: 15.2rem;
      border-radius: 0.2rem;
      height: auto;
    }
    > img:nth-child(2) {
      width: 5rem;
      height: auto;
      top: 6.7rem;
      left: -1.5rem;
    }
  }
}
