@import '../../theme.scss';

.circleIndicator {
  width: 4.8rem;
  height: 4.8rem;
  padding-top: 1.1rem;
  border: solid 0.05rem rgba(255, 255, 255, 0.8);
  border-radius: 2.4rem;
  @media (max-width: $breakpoint) {
    font-size: 0.8rem;
    width: 3.5rem;
    height: 3.5rem;
    border-radius: 1.75rem;
    padding-top: 0.75rem;
  }
  > div {
    width: 100%;
    text-align: center;
  }
  > div:nth-child(1) {
    font-size: 1.4rem;
    font-weight: 900;
    color: #ffffff;
    @media (max-width: $breakpoint) {
      font-size: 1.1rem;
    }
  }
  > div:nth-child(2) {
    font-size: 0.8rem;
    font-weight: 500;
    color: rgba(255, 255, 255, 0.64);
    @media (max-width: $breakpoint) {
      font-size: 0.6rem;
    }
  }
}
.clock {
  display: flex;
  > div + div {
    margin-left: 0.4rem;
  }
}
